<template>
  <div class="PixelMaster">
    <div class="status-header">
      <div>
      <span> {{$t('message.round')}}: {{ round + 1 }} </span>•
      <span> {{$t('message.total')}}: {{ info.pixel_drawn }} </span>•
      <span> {{$t('message.pot')}}: {{ new Number(info.pool_balance.toNumber()).toFixed(3) }} {{$t('message.key')}} </span>•
      <span v-if="false"> {{$t('message.last')}}: {{ info.last_painter.slice(0, 6) }} </span>
      <span> {{$t('message.difficulty')}}: {{ new Number(me.difficulty).toFixed(3) }} </span>
      <span v-if="hover_pixel != -1">• {{`(${hover_pixel%info.n_row}, ${~~(hover_pixel/info.n_row)})`}} </span>
      <span v-if="hover_pixel != -1">• {{$t('message.price')}}: {{ new Number(img_price[hover_pixel]).toFixed(4) }} </span>
      <span v-if="expected_cost != 0">• {{$t('message.cost')}}: {{ new Number(expected_cost || 0).toFixed(4) }} </span>
      </div>
    </div>
    <div class="main-container">
      <div class="middle-container">
        <div class="card svg-container">
          <div class="card-body card-shadow" ref="gameCanvas" style="padding: 0">
            <svg v-if="!show_heatmap" @drag.prevent="" style="margin: 0 auto; display: block" ref="canvas" :width="(width + 0.2) * img.n" :height="(width + 0.2) * img.n">
              <g v-for="(_, i) in info.n_col" :key="i" :transform="`translate(${i * (width + 0.2)}, 0)`">
                <g v-for="(_, j) in info.n_row" :key="j" :transform="`translate(0, ${j * (width + 0.2)})`">
                  <rect :width="width - 0.2" :height="width - 0.2" class="pixel"
                    :class="{ pending: img_pending[i + j * info.n_row]}"
                    :fill="img_temp_color[i + j * info.n_row] || img_color[i + j * info.n_row]"
                    @mouseover="pixelMouseover(i, j)"
                    @mouseout="pixelMouseout(i, j)"
                    @click="pixelClick(i, j)"
                  />
                </g>
              </g>
            </svg>
            <svg v-else @drag.prevent="" style="margin: 0 auto; display: block" ref="canvas" :width="(width + 0.2) * img.n" :height="(width + 0.2) * img.n">
              <g v-for="(_, i) in info.n_col" :key="i" :transform="`translate(${i * (width + 0.2)}, 0)`">
                <g v-for="(_, j) in info.n_row" :key="j" :transform="`translate(0, ${j * (width + 0.2)})`">
                  <rect :width="width - 0.2" :height="width - 0.2" class="pixel"
                    :fill="heatmap_color[~~((img_price[i + j * info.n_row] - min_price) / price_range * heatmap_color.length)]"
                    @mouseover="pixelMouseover(i, j)"
                    @mouseout="pixelMouseout(i, j)"
                  >
                  </rect>
                </g>
              </g>
            </svg>
          </div>
          <div class="toolbar">
            <div class="card-shadow toolbar1">
              <div class="button-wrapper">
                <button @click="toolbarShot" class="btn">
                  <img src="../assets/screen.png"/>
                  <span class="tool-tip" style="width: 100px">Screenshot</span>
                </button>
              </div>
              <div class="button-wrapper">
                <button @click="color_picker_show = !color_picker_show" class="btn">
                  <font-awesome-icon icon="pen"/>
                  <span :style="{ background: `${color_selected} none repeat scroll 0% 0%` }" class="PaintToolbar_colorIndicator"/>
                  <span class="tool-tip" style="width: 100px">ColorPicker</span>
                </button>
                <div class="PaintToolbar_colorPicker PaintToolbar_fourBit" v-show="color_picker_show">
                  <div class="ColorPicker_container">
                    <ol class="ColorPicker_fourBitColorBlocks">
                      <li v-for="(color, index) in color_candidate" :key="color" :title="color"
                        :class="{ disabled: index >= color_candidate_available }"
                        :style="{ background: `${color} none repeat scroll 0% 0%` }" class="ColorPicker_fourBitColorBlock"
                        @click="colorSelect(color)"/>
                    </ol>
                  </div>
                </div>
              </div>
              <div class="button-wrapper">
                <button @click="toolbarUndo" :disabled="pixel_task.length == 0 || is_watch_mode" class="btn">
                  <font-awesome-icon icon="reply"/>
                  <span class="tool-tip" style="width: 100px">Undo</span>
                </button>
              </div>
              <div class="button-wrapper">
                <button @click="toolbarRedo" :disabled="pixel_task_backup.length == 0 || is_watch_mode" class="btn">
                  <font-awesome-icon icon="share"/>
                  <span class="tool-tip" style="width: 100px">Redo</span>
                </button>
              </div>
              <div class="button-wrapper">
                <button @click="show_heatmap = !show_heatmap" class="btn">                  
                  <font-awesome-icon icon="fire"/>
                  <span class="tool-tip" style="width: 100px">Price Heatmap</span>
                </button>
              </div>
            </div>
            <div class="toolbar2">
                <button @click="toolbarSave" :disabled="pixel_task.length == 0 || me.team == 0 || is_watch_mode">
                  <img src="../assets/go.png" style="width: 100%"/>
                </button>
            </div>
          </div>
          <div class="result card-shadow">
            <div v-if="me.team != 0" class="button-wrapper">
                {{$t('message.current')}} {{info.current_infer_result}},
                <span v-if="expected_state">{{$t('message.win')}}</span>
                <span v-else>{{$t('message.fail')}}</span>
            </div>
            <div v-else-if="is_watch_mode" class="button-wrapper">
                {{$t('message.current')}} {{info.current_infer_result}}
            </div>
            <div v-else class="button-wrapper">{{$t('message.joinTeam')}}</div>
          </div>
        </div>
      </div>
      <div class="right-container">
        <div class="card">
          <div class="card-body card-shadow">
              <div class="card-line"><h3>{{$t('message.info')}}</h3></div>
              <div class="card-line card-bg card-bg1">
                <span class="m1 heavytext">
                  {{$t('message.total')}}
                  <span class="tool-tip">{{$t('message.accumulated').replace('$1', info.tx_limit)}}</span>
                </span>
                <span class="m2 progress">                  
                  <div class="progress-bar" :style="{width: info.pixel_drawn / info.tx_limit * 100 + '%'}"></div>
                  <span style="position: relative;top: -1px"> {{ info.pixel_drawn }} / {{ info.tx_limit }} </span>
                </span>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext"  id="recharge_span" for="payment-input">{{$t('message.recharge')}} </span>
                <input class="m1 form-control" id="payment-input" v-model="payment_value" type="number" min="0"/>
                <span class="card-key">{{$t('message.keys')}}<span class="tool-tip">1 {{$t('message.key')}} = 1 CTXC</span></span>
                <button type="button" :disabled="is_watch_mode" @click="clickPay" class="btn btn-sm card-btn">
                    {{$t('message.pay')}}
                </button>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="balance_span">{{$t('message.balance')}}</span>
                <span class="m1 heavytext" style="text-align: center">{{ personal_balance }}</span>
                <span class="card-key heavytext">{{$t('message.keys')}}<span class="tool-tip">1 {{$t('message.key')}} = 1 CTXC</span></span>
                <button type="button" :disabled="is_watch_mode" @click="clickWithdraw" class="btn btn-sm card-btn heavytext">
                    {{$t('message.withdraw')}}
                </button>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="stake_span">{{$t('message.stake')}}</span>
                <span class="m1 heavytext" style="text-align: center">{{ personal_stake }}</span>
                <span class="card-key heavytext">{{$t('message.keys')}}<span class="tool-tip">1 {{$t('message.key')}} = 1 CTXC</span></span>
                <span class="card-btn"></span>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="expected_return_span">{{$t('message.return')}}<span class="tool-tip">{{$t('message.ifWin')}}</span></span>
                <span class="m1 heavytext" style="text-align: center">{{ expected_return }}</span>
                <span class="card-key heavytext">{{$t('message.keys')}}<span class="tool-tip">1 {{$t('message.key')}} = 1 CTXC</span></span>
                <span class="card-btn heavytext"></span>
              </div>
              <div class="card-line"><h3>{{$t('message.team')}}</h3></div>
              <div class="card-line pk">
                <div class="red" :style="{
                  width: (info.number_of_pixel[0] + info.number_of_pixel[1]) == 0 ? 50 : (
                    info.number_of_pixel[0] / (info.number_of_pixel[0] + info.number_of_pixel[1]) * 100) + '%'}">
                </div>
              </div>
              <div class="card-line" style="display: flex;flex-wrap: wrap;justify-content: space-between;align-items: center;padding: 5px 8px">
                <button :disabled="me.team != 0 && me.team != 1 || is_watch_mode" @click="joinTeam(1)" type="button" class="btn btn-team btn-sm">
                  <img src="../assets/robot1.png"/>
                  <span class="tool-tip">{{$t('message.win1').replace('$1', '0,1,2,3,4')}}</span>
                </button>
                <img src="../assets/fight.png" width=40 height=40 />
                <button :disabled="me.team != 0 && me.team != 2 || is_watch_mode" @click="joinTeam(2)" type="button" class="btn btn-team btn-sm">
                  <img src="../assets/robot2.png"/>
                  <span class="tool-tip">{{$t('message.win2').replace('$1', '5,6,7,8,9')}}</span>
                </button>
              </div>
              <div class="card-line"><h3>{{$t('message.action')}}</h3></div>
              <div class="card-line card-bg">
                  <span class="ColorPicker_fourBitColorBlock" v-for="(d, index) in pixel_task" :key="index"
                      v-tooltip="`(${d[0]},${d[1]})`"
                      :style="{
                        background: `${d[2]} none repeat scroll 0% 0%`,
                      }">
                  </span>
              </div>
              <div class="card-line" v-if="false && own_address == contract_owner">
                <button
                @click="onContractReset()"
                type="button" class="btn btn-sm btn-label heavytext">
                    {{$t('message.reset')}}
                </button>
              </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="show_wallet">
      <div style="position: fixed;top: 0;bottom: 0;left: 0;right: 0;background: rgba(0,0,0,0.4);z-index: 50"></div>
      <div class="wallet">
        <div class="wallet-head">
            <h2>{{$t('message.welcome')}}</h2>
        </div>
        <div class="wallet-body">
            <h2>{{$t('message.tips')}}</h2><br>
            <h4 v-if="is_wallet_login">1. {{$t('message.login1')}} <a href="https://github.com/CortexFoundation/Cortex_Release" style="color: #0e7545">{{$t('message.install')}}</a> {{$t('message.login2')}}</h4>
            <h4 v-else>1. {{$t('message.login')}}</h4><br>
            <h4>2. {{$t('message.how')}} <a ></a></h4><br>
            <h2><button v-if="is_wallet_login" @click="show_wallet=false" class="btn">{{$t('message.continue')}}</button></h2>
        </div>
      </div>
    </div>

    <div class="loading" v-if="if_loading">
        <img src="../assets/loading.gif" width=50 height=50/>
        <br>
        <span v-if="gif_url=='pay'">Paying...</span>
        <span v-if="gif_url=='withdraw'">Withdrawing...</span>
        <span v-if="gif_url=='pending'">Pending...</span>
    </div>

    <div class="win" v-if="if_game_ending">
        <div class="win-container">
            <div class="win-head">
                <span class="close" @click="if_game_ending=false"><font-awesome-icon icon="times"/></span>
                <h2>INFER RESULT</h2>
                <h3>{{info.current_infer_result}}</h3>
            </div>
            <div class="win-body" v-show="false">
                <table>
                    <tr>
                        <td>Ranking</td>
                        <td>Address</td>
                        <td>Rewards</td>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>asdafdsadsfasdfadfa...</td>
                        <td>1234</td>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>asdafdsadsfasdfadfa...</td>
                        <td>1234</td>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>asdafdsadsfasdfadfa...</td>
                        <td>1234</td>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>asdafdsadsfasdfadfa...</td>
                        <td>1234</td>
                    </tr>
                </table>
            </div>
        </div>
    </div>

    <template>
      <notify-me :event-bus="bus">
          <template slot="content" scope="{data}">
              <div style="width: 100%; word-break: break-all; text-align: left">
                  <h4><b>{{ data.title }}</b></h4>
                  <p style="margin: 0">{{ data.text }}</p>
              </div>
          </template>
      </notify-me>
    </template>
  </div>
</template>

<script>
/*
1. Banner bar
2. Global status bar / total value / pixels drawn / pot / last painter(Date)
3. Private status bar / accumulated stake / team / recent actions
4. Toolbar
5. Infer result (infer historical result) / pixel_drawn / max pixel_drawn
6. Canvas / 28 * 28
*/
import Vue from 'vue';
import Web3 from "web3";
import jsonInterface from "../contract/jsoninterface.js";
import Notify from 'vue-notify-me';
import html2canvas from 'html2canvas';

const contract_address = "0xed7e60e05f98fc0419afd61d06b44dd84a5efebc";

const BN = Web3.utils.BN;
let ro_web3;
let wallet_web3;
/* eslint-disable */

function sleep(time) {
  return new Promise(resolve => setTimeout(resolve, time));
}

export default {
  name: "PixelMaster",
  data() {
    return {
      img: generateBinaryImage(),
      width: 15,
      hover_pixel: -1,
      current_pixel: null,
      payment_value: null,
      contract: null,
      contract_writable: null,
      instance: null,
      instance_addr: null,
      bus: new Vue(),
      color_candidate: [
        "#FFFFFF",
        "#7F7F7F",
        "#000000",
      ],
      contract_owner: "0xe50e4d03d6edda6abda52e3cdf6c9752d97d2242",
      color_selected: "#000000",
      color_candidate_available: 4,
      show_wallet: false,
      color_picker_show: false,

      pixel_task: [],
      pixel_task_backup: [],

      default_gturing_price: new BN("1000000000000000000"),

      info: {
        pixel_drawn: 0,
        tx_limit: 0,
        pool_balance: new BN(0),
        width: 28,
        height: 28,
        last_painter: "0x",
        current_infer_result: 0,
        channel: 1,
        growth_factor: 0,
        reimburse_factor: 0,
        fee_rate: 0,
        pool_factor: 0,
        number_of_pixel: [100, 100],
        number_of_player: [],
        total_stake: new BN(1000),
      },
      round: 0,
      min_price: 1,
      max_price: 1,
      price_range: 1,
      heatmap_color: ['#ffffb2','#fed976','#feb24c','#fd8d3c','#f03b20','#bd0026'],
      show_heatmap: false,

      me: {
        address: "",
        stake: new BN(0),
        team_stake: new BN(1000),
        balance: new BN(0),
        team: 0,
        difficulty: 1,
      },

      img_data: [],
      img_price: [],
      img_color: [],
      img_pending: [],
      img_temp_color: [],
      img_nbytes: 0,
      refreshing: false,

	  own_address: "0x0000000000000000000000000000000000000001",

      if_loading: false,
      if_game_ending: false,
      is_initialize: true,
      is_watch_mode: true,
      is_wallet_login: true,
      win_team: 1,
      win_return: 0,
      gif_url: null,
      is_Android: isAndroidDevice()?true:false,
      is_IOS: isIOSDevice()?true:false,
    };
  },
  watch: {
    pixel_task(val) {
      this.img_temp_color = [];
      for (let i = 0; i < val.length; ++i) {
        const index = val[i][0] + val[i][1] * this.info.n_row;
        this.img_temp_color[index] = val[i][2];
      }
    },
  },
  async mounted() {
    this.width = getWidth(this.img.n);

    let counter = 0;
    while (typeof ctxWeb3 == "undefined") {
      await sleep(50);
      ++counter;
      if (counter > 10) {
        break;
      }
    }

//    const ro_provider = new Web3.providers.HttpProvider("http://cerebro.waterhole.xyz:8080/");
    const ro_provider = new Web3.providers.HttpProvider("https://cerebro.cortexlabs.ai/wallet");
    ro_web3 = new Web3(ro_provider);
    if (typeof ctxWeb3 == "undefined") {
        if (this.is_Android) {
            console.log('android')
            //if (android_waterhole.getWalletAddr()) {
            //    this.is_watch_mode = false;
            //    this.own_address = android_waterhole.getWalletAddr();
            //}
        } else if (this.is_IOS){
            console.log('ios')
        } else {
            this.show_wallet = true;
        }
        wallet_web3 = ro_web3;
        this.contract = new ro_web3.eth.Contract(jsonInterface.digitalClash, contract_address);
    } else {
      this.is_watch_mode = false;
      this.is_wallet_login = false;

      if (!ctxWeb3.eth.defaultAccount) {
        this.show_wallet = true;
      }

      while (!ctxWeb3.eth.defaultAccount) {
        await sleep(50);
      }

      this.is_wallet_login = true;
      this.show_wallet = false;

      wallet_web3 = new Web3(ctxWeb3.currentProvider);
      this.contract = new ro_web3.eth.Contract(jsonInterface.digitalClash, contract_address);
      this.contract_writable = new wallet_web3.eth.Contract(jsonInterface.digitalClash, contract_address);
      this.own_address = ctxWeb3.eth.defaultAccount;

      console.log("Init ctxWeb3 successfully.");
    }
    this.initialVariable();
    setInterval(() => this.refreshVariable(), 3000);
  },
  components: {
    'notify-me': Notify
  },
  computed: {
    expected_return() {
      try {
        const value = this.me.stake
          .mul(this.info.pool_balance)
          .mul(new BN(1000))
          .div(this.me.team_stake)
          .toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    personal_balance() {
      try {
        const value = this.me.balance.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    personal_stake() {
      try {
        const value = this.me.stake.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    expected_state() {
      try {
        return this.win_team == this.me.team;
      } catch (e) {
        return 0;
      }
    },
    expected_cost() {
      try {
        let ret = 0;
        for (let i = 0; i < this.pixel_task.length; ++i) {
          let idx = this.pixel_task[i][0] + this.pixel_task[i][1] * this.info.n_row;
          ret += this.img_price[idx];
        }
        return ret;
      } catch (e) {
        return 0;
      }
    },
  },
  methods: {
    getPersonalBalance() {
      try {
        const value = this.me.balance.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    getPersonalStake() {
      try {
        const value = this.me.stake.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    updateImgData() {
      for (let i = 0; i < this.img_data.length; ++i) {
        const v = 255 - this.img_data[i];
        this.img_color[i] = `rgb(${v},${v},${v})`;
      }
    },
    toolbarUndo() {
      let n = this.pixel_task.length;
      this.pixel_task_backup.push(this.pixel_task.splice(n - 1, 1)[0]);
    },
    toolbarRedo() {
      let n = this.pixel_task_backup.length;
      this.pixel_task.push(this.pixel_task_backup.splice(n - 1, 1)[0]);
    },
    toolbarShot() {
      html2canvas(document.body).then(function(canvas){
        var url = canvas.toDataURL();
        download(url);
      })
    },
    async initialVariable() {
      this.instance_addr = await this.contract.methods
        .getLatestInstance()
        .call({ from: this.own_address });

      this.instance = new ro_web3.eth.Contract(jsonInterface.digitalClashInstance, this.instance_addr);

      this.token_addr = await this.contract.methods
        .token()
        .call({ from: this.own_address });
      this.token = new wallet_web3.eth.Contract(jsonInterface.digitalClashToken, this.token_addr);

      this.contract_owner = await this.contract.methods
        .owner()
        .call({ from: this.own_address });

      const d = await this.instance.methods
        .getConstant()
        .call({ from: this.own_address });

      this.info.n_row = +d[0];
      this.info.n_col = +d[1];
      this.info.n_ch = +d[2];
      this.info.tx_limit = +d[3];
      this.info.growth_factor = +d[4];
      this.info.reimburse_factor = +d[5];
      this.info.pool_factor = +d[6];
      this.default_gturing_price = new BN(d[7]);
      this.img_nbytes = this.info.n_row * this.info.n_col * this.info.n_ch;
      this.img_data = [];
      for (let i = 0; i < this.img_nbytes; ++i) {
        this.img_data.push(0);
      }
      this.updateImgData();
    },
    onContractReset() {
      /*
      this.contract.methods
        .reset()
        .send({ from: this.own_address })
        .then(d => {
          alert("contract reset finish");
        });
      */
    },
    async refreshVariable() {
      if (!this.instance) {
        return;
      }

      if (this.refreshing) {
        return;
      } else {
        this.refreshing = true;
      }

      let d;

      let data = await this.instance.methods
        .img()
        .call({ from: this.own_address });

      this.img_data = [];
      for (let i = 2; i < data.length; i += 2) {
        this.img_data.push(parseInt(data.substring(i, i + 2), 16));
      }
      this.updateImgData();

      d = await this.instance.methods
        .getInfo()
        .call({ from: this.own_address });

      const last_infer_result = this.info.current_infer_result;
      const personal_team = this.me.team;
      const last_expected_return = this.expected_return;

      this.info.pixel_drawn = +d[0];
      this.info.number_of_player = [+d[1], +d[2]];
      this.info.pool_balance = (new BN(d[3])).div(this.default_gturing_price);
      this.info.current_infer_result = +d[4];
      this.info.last_painter = d[5];
      const stake1 = new BN(d[6]);
      const stake2 = new BN(d[7]);
      this.info.number_of_pixel = [stake1.div(this.default_gturing_price).toNumber(), stake2.div(this.default_gturing_price).toNumber()];
      this.info.total_stake = stake1.add(stake2);
      if (this.me.team == 1) {
        this.me.team_stake = stake1;
      } else if (this.me.team == 2) {
        this.me.team_stake = stake2;
      } else {
        this.me.team_stake = new BN(0);
      }
      this.if_game_ending = +d[8];
      this.win_team = +d[9];

      d = await this.instance.methods
        .getPersonalInfo()
        .call({ from: this.own_address });

      this.me.balance = new BN(d[0]);
      this.me.stake = new BN(d[1]);
      this.me.team = +d[2];
      this.me.difficulty = +d[3] / 1000;
      if (this.me.difficulty == 0) {
        this.me.difficulty = 1;
      }
      this.win_return = this.me.stake;

      this.adjustCanvas();

      data = await this.instance.methods
        .txCount()
        .call({ from: this.own_address });

      this.img_price = [];
      let min_price = 1e10, max_price = 0;
      console.log(this.me.difficulty);
      for (let i = 2; i < data.length; i += 2) {
        let tx_count = parseInt(data.substring(i, i + 2), 16);
        let factor = this.info.growth_factor.valueOf() / 1000;
        const price = Math.pow(factor, tx_count) * this.me.difficulty;
        this.img_price.push(price);
        if (price < min_price) {
          min_price = price;
        } else if (price > max_price) {
          max_price = price;
        }
      };
      this.min_price = min_price;
      this.max_price = max_price;
      this.price_range = max_price - min_price + 1e-4;

      this.if_loading = false;
      this.refreshing = false;
      this.is_initialize = false;
    },
    adjustCanvas() {
      if (!this.$refs.gameCanvas) return;
      const bounding = this.$refs.gameCanvas.getBoundingClientRect();
      //const width = Math.min(bounding.width, bounding.height);
      const width = bounding.width;
      this.width = (width - 12) / this.img.n - 0.2;
    },
    joinTeam(i) {
      if (i == this.me.team) return;
      this.if_loading = true;
      this.contract_writable.methods
        .selectTeam(this.instance_addr, i)
        .send({ from: this.own_address })
        .then((receipt) => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    clickPay() {
      this.if_loading = true;
      this.gif_url = "pay";
      this.token.methods
        .pay(contract_address)
        .send({
          from: this.own_address,
          value: new BN(this.payment_value).mul(this.default_gturing_price)
        })
        .then(receipt => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    clickWithdraw() {
      this.if_loading = true;
      this.gif_url = "withdraw";
      this.token.methods
        .withdraw(
          "0x" + this.me.balance.toJSON()
        )
        .send({ from: this.own_address })
        .then(d => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    colorSelect(color) {
      const index = this.color_candidate.indexOf(color);
      if (index == -1 || index >= this.color_candidate_available) {
        this.color_picker_show = false;
        return;
      }
      this.color_selected = color;
      this.color_picker_show = false;
    },
    pixelMouseover(i, j) {
      this.hover_pixel = i + j * this.info.n_row;
    },
    pixelMouseout(i, j) {
      if (this.hover_pixel == i + j * this.info.n_row) {
        this.hover_pixel = -1;
      }
    },
    lowBalanceWarning() {
      this.pixel_task.pop();
      this.bus.$emit('notify-me', {
        status: 'is-danger',
        data: {
            title: this.$t('message.error'),
            //text: `Estimated cost will exceed your balance`,
            text: this.$t('message.exceed'),
        }
      });
    },
    pixelClick(i, j) {
      if (this.is_watch_mode) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: this.$t('message.error'),
              //text: `The watch mode does not allow any pixel operation.`,
              text: this.$t('message.watch'),
          }
        });
        return;
      }
      if (this.img_pending[i + j * this.info.n_row]) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: this.$t('message.error'),
              //text: `Drawing task on this pixel is still pending`,
              text: this.$t('message.drawing'),
          }
        });
        return;
      }
      if (this.img_temp_color[i + j * this.info.n_row]) {
        this.img_temp_color[i + j * this.info.n_row] = this.color_selected;
        for (let k = 0; k < this.pixel_task.length; ++k) {
          if (this.pixel_task[k][0] == i && this.pixel_task[k][1] == j) {
            this.pixel_task.splice(k, 1);
            break;
          }
        }
      }
      this.pixel_task.push([i, j, this.color_selected]);
      if (this.pixel_task.length > 15) {
        this.pixel_task.pop();
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: this.$t('message.error'),
              //text: `you cannot modify more than 10 pixels in one operation`,
              text: this.$t('message.operation'),
          }
        });
        return;
      }
      if (this.expected_cost > this.personal_balance) {
        this.lowBalanceWarning();
        return;
      }
      this.pixel_task_backup = [];
    },
    async toolbarSave() {
      this.if_loading = true;
      this.gif_url = "pending";
      let ops = [];

      const tmp_task = [];
      for (let i = 0; i < this.pixel_task.length; ++i) {
        ops.push(this.pixel_task[i][0]);
        ops.push(this.pixel_task[i][1]);
        let color = 255 - parseInt(this.pixel_task[i][2].substring(0, 2), 16);
        ops.push(color);
        tmp_task.push(this.pixel_task[i]);
        const index = this.pixel_task[i][0] + this.pixel_task[i][1] * this.info.n_row;
        this.img_pending[index] = 1;
      }
      this.pixel_task = [];

      try {
        const d = await this.contract_writable.methods
          .draw(this.instance_addr, ops)
          .send({ from: this.own_address });
      } catch (err) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: this.$t('message.error'),
              //text: 'Transaction cancelled.',
              text: this.$t('message.cancelled'),
          }
        });
      }
      for (let i = 0; i < tmp_task.length; ++i) {
        const index = tmp_task[i][0] + tmp_task[i][1] * this.info.n_row;
        this.img_pending[index] = 0;
      }
    }
  },
  props: {
    msg: String
  }
};

function generateBinaryPixel() {
  return {
    data: {
      msg: "no message",
      price: 1,
      color: 0

    },
    hover: false
  };
}

function generateBinaryImage(n) {
  n = n || 28;
  const img = [];
  for (let i = 0; i < n; ++i) {
    const row = [];
    for (let j = 0; j < n; ++j) {
      let pixel = generateBinaryPixel();
      pixel.position = [i, j];
      row.push(pixel);
    }
    img.push(row);
  }
  return {
    data: img,
    n: n
  };
}

function download(url) {
    var el = document.createElement('a');
    el.setAttribute('href', url);
    el.setAttribute('download', 'pixelmaster');
    el.click();
    el.remove();
}

function isAndroidDevice() {
    return navigator.userAgent.match(/Android/i)
}

function isIOSDevice() {
    return navigator.userAgent.match(/iPhone|iPad|iPod/i)
}

function getWidth(num) {
    var box = document.getElementsByClassName('svg-container')[0];
    var box_width = box.clientWidth || box.offsetWidth;
    var width = (box_width - 12) / num - 0.2;
    return width;
}
